	module.exports = {"data":{"markdownRemark":{"html":"<p>Verket be spilt til Griegs avgangseksamen ved konservatoriet i Leipzig i 1862, men Grieg selv var ikke fornøyd med verket. Han omtalte senere verket som «elevmessig» og at det hadde vært best om det hadde vært «..overlatt til fullstendig glemsel».  </p>\n<p>Høsten 1858 reiste en ung norsk student med dampskipet fra Bergen til Hamburg, medbrakt noter, klær og personlige eiendeler. Ferden gikk videre med tog til Leipzig, der han skulle bli kunstner. Med Ole Bull som inspirator og med sine foreldres velsignelse reiste den 15 år gamle Edvard Grieg nå hjemmefra for å begynne på sitt voksne liv.</p>\n<p>Den unge Grieg ble tatt opp som elev ved det berømte Musikkonservatoriet i Leipzig, og hadde store forventninger til studiet. Herfra skulle han komme hjem til Norge «som en heksemester i tonernes rike». Men den rigide og konservative undervisningen passet dårlig for Grieg. Riktignok var han som barn vant til at moren, hans første pianolærer, var en skarp iakttager, og et korrektiv til hans drømmerier ved instrumentet. Men etter en tid med sin første pianolærer i Leipzig, gikk Grieg rett til konservatoriets direktør og forlangte å bli fritatt fra Louis Plaidys undervisning. Et dristig grep av en nykommer. Grieg selv innrømmet senere at han i begynnelsen var en lat elev, og at hans lærer hadde rett da han kastet notene gjennom rommet og med tordnende stemme utbrøt: «Gå hjem og øv!».</p>\n<p>Hans nye lærer, Schumann-vennen Ernst Ferdinand Wenzel, satte et større mot i Grieg, og han fikk også ta timer hos den berømte Ignaz Moscheles. Hos ham fikk Grieg innstudere «et dusinvis av Beethovens sonater», og Moscheles’ egne etyder. Brikkene falt på plass og den unge Grieg fikk gode skussmål.</p>\n<p>Resten er musikkhistorie. En stor del av Griegs komposisjoner er viet pianoet. Ti hefter med lyriske stykker, 66 i alt, har blitt stående som hans musikalske livshistorie. Flere opus med folketonebearbeidelser, den store G-mollballaden, Holbergsuiten og – ikke minst – den hyppig spilte Klaverkonserten i a-moll har satt Griegs navn på det musikalske verdenskartet.</p>\n<p>Grieg gikk hjem og øvde. Men opplevelsene av studietiden var blandet, og han hevdet senere at han kom hjem fra Leipzig «omtrent like dum, som jeg var kommet derind». Helt sant var nok ikke dette, studieårene ga ham rike muligheter for å ta til seg av byens rike kulturliv. De mange konsertbesøkene ble svært viktige, og «…utviklede min ånd og min musikalske dømmekraft i høieste grad». Men først i København-årene som fulgte fikk hans fantasi fritt spillerom, og han kunne for alvor søke etter en egen musikalsk identitet. Motgangen ble en kilde til å stole på egen kraft, og til å arbeide selvstendig.</p>","timeToRead":2,"excerpt":"Verket be spilt til Griegs avgangseksamen ved konservatoriet i Leipzig i 1862, men Grieg selv var ikke fornøyd med verket. Han omtalte…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/opus1.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":"Fire pianostykker","dedicatedTo":"Ernst Ferdinand Wenzel","artists":["Edvard Grieg"],"medialink":null,"composedin":"1861","title":"1","author":"Test Testesen","date":"2017-12-04T12:28:12.048Z","category":"opus","tags":["piano"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/room1-1fdeae0024f1c9486259093579ce4241-47774.jpg","srcSet":"/static/room1-1fdeae0024f1c9486259093579ce4241-45ad6.jpg 480w,\n/static/room1-1fdeae0024f1c9486259093579ce4241-e2fcf.jpg 960w,\n/static/room1-1fdeae0024f1c9486259093579ce4241-47774.jpg 1920w,\n/static/room1-1fdeae0024f1c9486259093579ce4241-93088.jpg 2880w,\n/static/room1-1fdeae0024f1c9486259093579ce4241-bfc0d.jpg 3840w,\n/static/room1-1fdeae0024f1c9486259093579ce4241-415bd.jpg 4854w","sizes":"(max-width: 1920px) 100vw, 1920px"}}}},"fields":{"slug":"/opus1/","artist":null,"opuses":null}}},"pathContext":{"slug":"/opus1/"}}

